.homepage-news-section {
    padding-top: 25px;

    .article-item {
        width: 70%;
        float: left;

        h2{
            margin: 0;
            font-size: 32px;
        }
    }
    .testimonial-item {
        width: 30%;
        float: left;
        padding-left: 70px;
        box-sizing: border-box;
        padding-top: 10px;

        h2{
            font-size: 19px;
            text-transform: uppercase;
            margin: 0;
        }

        p{
            font-size: 14px;
        }

        cite{
            float: right;
            font-size: 14px;
        }

    }
}
